	module.exports = {"data":{"nimblRenderedHtml":{"html":"<h1 id=\"vita\">Vita</h1>\n<p>Gadda nasce a Milano nel 1893, studia <strong>ingegneria</strong> all'università.</p>\n<p><mark>Nel 1915 partecipa da volontario alla Grande Guerra</mark>, in questo periodo scrive un diario di guerra.<br>\nGadda conviveva con il dubbio di non valere nulla, Gadda sperava nella guerra di avere un'occasione di riscatto, ma non fu così.</p>\n<p>A Caporetto viene fatto prigioniero e quando torna a casa a guerra finita scopre che il fratello è morto, questo fu per lui un trauma fortissimo che alterò il suo rapporto con la patria e il patriottismo.</p>\n<p>Per un periodo si trasferisce in Argentina, dove lavora come ingegnere.</p>\n<p>Nel Giugno 1926 pubblica i suoi primi 4 racconti, in uno di questi, <em><strong>La morte di Puk</strong></em>, tratta il tema della morte attraverso la morte di un cane che rappresenta tutte le morti.</p>\n<p>Negli anni 30 inizia ad imporsi sulla scena letteraria e nel 1938 inizia a pubblicare a puntate <em><strong>La Cognizione del Dolore</strong></em>, per il quale nel 1963 vince il Prix International de Literature.</p>\n<p>Nel 1944 lascia Firenze e scrive un <strong>trattato antifascista</strong>. Descrive Mussolini in maniera dispregiativa e l'Italia come sottomessa sessualmente alla virilità di Mussolini.</p>\n<p>Lavora un anno alla rai ma poi smette per concentrarsi sul pasticciaccio.</p>\n<p>Nel momento del successo Gadda <mark>inizia ad avere manie di persecuzione e smette di scrivere per sempre</mark> (link a manzoni), sistema invece editorialmente i suoi vecchi scritti.</p>\n<h1 id=\"temi-e-stile\">Temi e stile</h1>\n<p><mark>L'opera di Gadda tende a concentrarsi su pochi temi fondamentali</mark>, è ossessionato dalla morte, dal male, dalla famiglia, dalla guerra, dal rapporto con la madre, ma anche da molte piccole cose che risalgono alla sua vita privata e sociale.</p>\n<blockquote>\n<p>il pasticcio e il disordine mi annientano e non posso fare nulla se non vi è ordine</p>\n</blockquote>\n<p>Gadda è sia scrittore che filosofom autodidatta.<br>\nDal punto di vista filosofico si avvicina a <strong>Wittgestein</strong>, non arriva mai ad un sistema: sono meditazioni.<br>\nLa fonte primaria della sua filosofia è se stesso.</p>\n<p>Scrive <em><strong>La meditazione milanese</strong></em>, Gadda stabilisce un nesso tra tutte le cose materiali e immateriali, <mark>ogni oggetto è comprensibile solo se scomposto nelle sue minime parti e se al contempo è inserito nel contesto di tutte le sue relazioni</mark>.<br>\nLa ricerca della verità per Gadda sembra essere una forma di nevrosi.</p>\n<p>In molti affermano che Gadda sia uno scrittore <strong>barocco</strong>, Gadda risponde invece che è il mondo ad essere barocco.</p>\n<p>In lui <mark>convivono una forte volontà ordinatrice e la tendenza ad aggiungere dettagli</mark>.</p>\n<p>L'elemento caratteristico di Gadda è la <strong>poltiglia linguistica</strong> e il plurilinguismo</p>\n<h1 id=\"opere\">Opere</h1>\n<p>In 2 mesi scarsi Gadda riempie tre quaderni di riflessioni filosofiche, un testo chiave pubblicato dopo la sua morte, essenziale per capire le ragioni dietro la sua vita letteraria.</p>\n<p>Inizia ad essere scrittore a tempo pieno nel 1940</p>\n<h2 id=\"la-cognizione-del-dolore\">La Cognizione del Dolore</h2>\n<p>Nel 1937 inizia a scrivere <em>La cognizione del Dolore</em>. Fu molto piu che un romanzo, fu un processo interamente interpretato da Gadda.</p>\n<p>Il romanzo esce in 7 puntate su una rivista fiorentina ed è ambientato in un villaggio sudamericano.</p>\n<p>Il fratello è un personaggio ripreso più volte nel romanzo.</p>\n<p>Utilizza la metafora dei Maiali per i contadini. Gadda ama la pace, la tranquillità, non vuole avere attorno maiali (contadini).</p>\n<p>La narrazione è vista come strumento per scoprire la sia verità, riscattare la sua inesistita giovinezza.</p>\n<h2 id=\"quer-pasticciaccio-brutto-de-via-merulana\">Quer pasticciaccio brutto de via Merulana</h2>\n<p>La stesura del pasticciaccio dura molti anni e si ispira ad un fatto di cronaca.</p>\n<p>Il romanzo parla di Don Ciccio, che indaga su un <strong>furto di gioielli e omicidio</strong> ai danni di una donna di sua conoscenza, il tutto svelanto gli intrighi e i misteriosi sobborghi della Roma fascista.</p>\n<p>Il romanzo poliziesco classico è consolatorio, alla fine si scopre il colpevole, ma la vita non lo è. Per questo, non per le condizioni di salute, il pasticciaccio è incompleto.<br>\n<mark>Gadda si è arreso al caos del mondo</mark>.</p>\n<h1 id=\"letture-pasticciaccio\">Letture pasticciaccio</h1>\n<h2 id=\"capitolo-6\">Capitolo 6</h2>\n<p><strong>ekfrasis</strong> → descrizione di opere d'arte. In relazione al <em>visibile parlare</em> della cornici intarsiate del purgatorio</p>\n<p><mark>L'ekfrasis di Gadda interpreta erroneamente il quadro</mark> dicendo che il personaggio sia un dottore, mentre è un pittore; questo errore è voluto, Gadda conosceva infatti molto bene il dipinto.<br>\nL'immagine ritratta è in realtà Zeusi che per ritrarre la bellezza utilizza una <strong>docta varietas</strong>: utilizza più modelle prendendo le parti migliori di ognuna.</p>\n<p><mark>La docta varietas di Gadda passa attraverso il fraintendimento</mark>.</p>\n","frontmatter":{"title":"Gadda","pdfCompletoPath":null,"draft":false},"headings":[{"value":"<p>Vita</p>\n","depth":1,"rawText":"Vita\r"},{"value":"<p>Temi e stile</p>\n","depth":1,"rawText":"Temi e stile\r"},{"value":"<p>Opere</p>\n","depth":1,"rawText":"Opere\r"},{"value":"<p>La Cognizione del Dolore</p>\n","depth":2,"rawText":"La Cognizione del Dolore\r"},{"value":"<p>Quer pasticciaccio brutto de via Merulana</p>\n","depth":2,"rawText":"Quer pasticciaccio brutto de via Merulana\r"},{"value":"<p>Letture pasticciaccio</p>\n","depth":1,"rawText":"Letture pasticciaccio\r"},{"value":"<p>Capitolo 6</p>\n","depth":2,"rawText":"Capitolo 6\r"}]}},"pathContext":{"category":"Italiano","title":"Gadda"}}

